.wpsvs {
  position: fixed;
  bottom: 0;
  margin: auto;
  left: 0;
  right: 0;
  width: 50%;
  min-height: 150px;
  background: #fff;
  box-shadow: #ff9249 0px 8px 36px 0px;
  box-sizing: border-box;
  z-index: 99; }
  .wpsvs .title {
    font-size: 15px;
    padding: 5px 10px;
    background: #ff9249;
    color: #fff; }
    .wpsvs .title .close-sugs {
      float: right;
      position: relative;
      top: 0px;
      font-weight: bold;
      cursor: pointer; }
  .wpsvs .items {
    margin: 25px 30px; }
    .wpsvs .items .item {
      margin-right: 10px;
      text-align: center;
      border: 1px solid rgba(255, 152, 0, 0.75);
      border-radius: 0px 0px 20px;
      transition: all .5s; }
      .wpsvs .items .item a {
        color: black; }
        .wpsvs .items .item a img {
          width: 100%;
          padding: 0px 0px 1px;
          background: rgba(255, 152, 0, 0.75); }
      .wpsvs .items .item:hover {
        box-shadow: 9px 5px 13px -10px rgba(255, 152, 0, 0.75);
        -webkit-box-shadow: 9px 5px 13px -10px rgba(255, 152, 0, 0.75);
        -moz-box-shadow: 9px 5px 13px -10px rgba(255, 152, 0, 0.75); }
  .wpsvs .slick-next,
  .wpsvs .slick-prev {
    display: none !important; }
